html.w-mod-touch * {
    background-attachment: scroll!important;
}
body{
    margin: 0;
    min-height: 100%;
    background-color: #fff;
    font-family: Arial,sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: #333;
}

.menu-button svg{
    width: 18px !important;
    height: 18px !important;
}

.current {
    color: #de3831;
}

.w-nav {
    position: relative;
    background: #ffffff;
    z-index: 1000;
}

.navbar_x {

    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: auto;
    margin-left: 0;
    padding-top: 0;
    padding-right: 0;
    padding-left: 0;
    clear: none;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    background-color: #fff;
}

.w-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 940px;
}


.brand {
    position: relative;
    display: block;
    margin-right: auto;
    padding-top: 0;
    padding-bottom: 0;
}
.brand.w--current {
    padding-top: 0;
}
.logo {
    display: inline-block;
    color: transparent;
    text-align: left;
}
.w-nav-brand {
    position: relative;
    float: left;
    text-decoration: none;
    color: #333;
}

.w-nav-button {
    position: relative;
    float: right;
    padding: 22px;
    margin-top: -6px;
    font-size: 24px;
    display: none;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    tap-highlight-color: rgba(0,0,0,0);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.w-nav-menu {
    position: relative;
    float: right;
}
.nav-menu {
    margin-left: 0;
    padding-left: 0;
    clear: none;
}

.w-nav-link {
    position: relative;
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    color: #222;
    padding: 20px;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    max-width: 1280px;
}

.w-nav-link:hover {
    text-decoration: none;
    transition: 0.5s;
    color: rgba(195,59,61,255)
}

.nav-works {
    font-family: 'IBM Plex Sans',sans-serif;
    text-transform: uppercase;
}
.nav-company {
    margin-left: 0;
    padding-top: 20px;
    font-family: 'IBM Plex Sans',sans-serif;
    text-transform: uppercase;
}
.nav-careers {
    font-family: 'IBM Plex Sans',sans-serif;
    text-transform: uppercase;
}
.nav-works.contact-button {
    border-width: 1px;
    border-color: #333;
    background-color: transparent;
}

.container_x {
    display: flex;
    max-width: 1280px;
    min-width: 80%;
    margin-right: auto;
    margin-bottom: 0;
    margin-left: auto;
    padding-top: 0;
    padding-right: 5%;
    padding-left: 5%;
    clear: none;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    grid-auto-columns: 1fr;
    /*grid-column-gap: 16px;*/
    grid-row-gap: 16px;
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    -ms-grid-rows: auto auto;
    grid-template-rows: auto auto;
    background-color: #fff;
    text-align: left;
}

.collapsed{
    display:none
}

.contact-button {
    margin-top: 8px;
    padding: 9px 15px;
    border: 2px solid #de3831;
    border-radius: 20px;
    background-color: transparent;
    font-family: 'IBM Plex Sans',sans-serif;
    color: #333;
    text-decoration: none;
    text-transform: uppercase;
}

@media (max-width: 991px) {
    .container_x {

        grid-column-gap: 16px;
    }
    .collapsed{
        display:flex;
    }
    /*.w-container {*/
    /*    max-width: 728px;*/
    /*}*/
    .upper_nav{
        display: none;
    }
    .w-nav-link{
        margin-right: 0;
        margin-left: 0;
    }
    .navbar_x {
        max-height: 60px;
    }

    .brand {
        margin-left: 20px;
        padding-top: 0;
    }
    .logo {
        max-width: 88px;
        margin-left: 16px;
    }
    .menu-button {
        background-color: #fff;
    }
    .w-nav-button {
        display: block;
    }
    .nav-works.contact-button {
        margin-top: 0;
        padding: 20px;
        border-style: none;
        border-radius: 0;
        font-weight: 600;
    }
    .w-nav-menu {
        display: flex;
        z-index: -10;
        transform: translateY(-600px) translateX(0);
        transition: transform 700ms ease 0s;
        flex-flow: column wrap;
        text-align: left;
        background-color: #fff;
        box-shadow: 0 5px 5px 0 rgb(50 50 50 / 50%);
        color: #000;
        float: none;
        text-align: left;
    }
    .w-nav-menu.active{
        z-index: -10;
        transform: translateY(0px) translateX(0px);
    }

    .container_x {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        padding-right: 0;
        padding-left: 0;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
    }
}

@media screen and (max-width:767px){
    .brand {
        padding-left: 0;
    }

}

@media screen and (max-width:479px){
    .menu-button svg{
        width: 17px !important;
        height: 17px !important;
    }
    .navbar_x {
        display: block;
        max-height: 60px;
    }
    .container_x {
        height: 60px;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        min-width: 31%;
        padding-right: 0;
        padding-left: 0;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
    }
    .brand.w--current {
        max-width: 50vw;
        margin-left: 20px;
        padding-top: 0;
        padding-left: 0;
    }
    .nav-works.contact-button {
        margin-top: 0;
        padding: 20px;
    }
    .logo {
        max-width: 66px;
    }
}