.w-inline-block_x {
    max-width: 100%;
    display: inline-block;
}



.link-block-25 {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-decoration: none;
}

.reali-section {
    display: block;
    overflow: visible;
    height: 65vh;
    min-height: 700px;
    font-family: 'IBM Plex Sans',sans-serif;
}
.reali-slider {
    height: 100%;
    background-image: linear-gradient(
            45deg
            ,#ede2e2,#eff0f4);
}

.bold-text {
    font-family: 'IBM Plex Sans',sans-serif;
    color: #333;
    font-weight: 400;
}

.reali-log {
    position: static;
    display: inline-block;
    max-width: 83px;
    margin-right: auto;
    margin-bottom: 10px;
    margin-left: auto;
    text-align: center;
}

.reali-image-container {
    position: static;
    left: auto;
    right: auto;
    display: flex;
    width: auto;
    height: 400px;
    padding-left: 0;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    flex: 1;
    text-align: center;
}

.image-36 {
    max-height: 100%;
    max-width: none;
}

.link-block-23 {
    display: flex;
    height: 400px;
    max-height: none;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.reali-image-container.section-sizing {
    flex: 0 auto;
}

.reali-col {
    display: flex;
    width: 100%;
    height: 100%;
    margin-right: 0;
    margin-left: 0;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

.reali-content-container {
    width: 100%;
    margin-top: -75px;
}

.reali-content {
    position: static;
    left: auto;
    display: block;
    margin-top: 0;
    margin-right: 0;
    margin-left: 0;
    padding-top: 0;
    text-align: center;
}


.reali-paragraph {
    width: auto;
    height: auto;
    max-width: 620px;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
    font-family: 'IBM Plex Sans',sans-serif;
    text-align: center;
}
.reali-paragraph p{
    margin-top: 0;
    margin-bottom: 10px;
}
.tags-div-block {
    display: flex;
    width: auto;
    max-width: none;
    margin: 10px auto 20px;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;
    align-content: space-between;
    font-size: 16px;}

.tags-div-block.fcg, .tags-div-block.tags-div-reali {

    justify-content: center;
}
.reali-button {
    display: inline-block;
    width: 250px;
    height: 56px;
    margin-top: 7px;
    padding: 15px 25px;
    border: 0 #de3831;
    border-radius: 28px;
    background-color: transparent;
    background-image: linear-gradient(
            135deg
            ,#d76464,#8a5b98);
    box-shadow: 0 16px 37px -16px rgb(104 17 46 / 50%);
    font-family: 'IBM Plex Sans',sans-serif;
    color: #fff;
    font-size: 20px;
    line-height: 26px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
}
.reali-heading {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
    color: #000;
    font-size: 36px;
    line-height: 40px;
    text-align: center;
}
.w-button {
    display: inline-block;
    padding: 9px 15px;
    background-color: #3898ec;
    color: #fff;
    border: 0;
    line-height: inherit;
    text-decoration: none;
    cursor: pointer;
    border-radius: 0;
}

.tags-home {
    overflow: visible;
    height: 26px;
    margin-top: 5px;
    margin-right: 10px;
    margin-bottom: 5px;
    padding: 3px 8px;
    border-radius: 4px;
    background-color: rgba(12,10,48,.33);
    font-size: 16px;
    text-align: center;
}

.slider-container {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    min-height: auto;
    padding: 5% 5% 45px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}


@media screen and (max-width: 991px) {
    .reali-section {
        height: 100vh;
        min-height: 700px;
    }
    .reali-image-container {
        position: static;
        display: flex;
        width: 100%;
        height: 50%;
        padding-left: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .reali-col {
        margin-right: 0;
        margin-left: 0;
        justify-content: flex-end;
    }
    .reali-image-container.section-sizing {
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        -ms-flex: 1;
        flex: 1;
    }
    .image-36 {
        max-width: 100%;
    }
    .link-block-23 {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
    }
    .reali-content-container {
        margin-top: -210px;
        -webkit-box-flex: 0;
        -webkit-flex: 0 auto;
        -ms-flex: 0 auto;
        flex: 0 auto;
    }
    .reali-content {
        position: static;
        top: -64px;
        margin-top: 0;
        padding-top: 30px;
    }
    .reali-paragraph {
        max-width: 87%;
    }
    .tags-div-block {
        justify-content: flex-start;
    }
    .tags-div-block.tags-div-reali {
        justify-content: center;
    }
    .reali-button {
        margin-top: 15px;
    }
    .slider-container {
        min-height: 0;
        padding-bottom: 45px;
    }
}
@media screen and (max-width: 767px) {
    .reali-section {
        height: 800px;
    }
    .reali-image-container {
        width: 100%;
        height: 50%;
        padding-left: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: center;
        flex: 1;
    }
    .reali-col {
        width: 100%;
        margin-right: 0;
        margin-left: 0;
        flex-direction: column;
        justify-content: space-between;
        align-self: stretch;
    }
    .link-block-23 {
        display: flex;
        justify-content: center;
    }
    .image-36 {
        max-width: 100%;
    }
    .reali-content-container {
        position: static;
        left: -3%;
        display: flex;
        width: 100%;
        height: auto;
        max-width: none;
        margin-top: 0;
        margin-bottom: 0;
        flex-direction: column;
        justify-content: flex-end;
        align-self: center;
    }
    .reali-content {
        margin-top: 0;
        padding-top: 0;
        text-align: left;
    }
    .link-block-25 {
        -webkit-box-pack: start;
        -webkit-justify-content: flex-start;
        -ms-flex-pack: start;
        justify-content: flex-start;
    }
    .reali-paragraph {
        width: auto;
        max-width: none;
        padding-left: 0;
        text-align: left;
    }
    .tags-div-block {
        max-width: none;
        justify-content: flex-start;
    }
    .tags-div-block.fcg, .tags-div-block.tags-div-reali {
        justify-content: flex-start;
    }
    .adomi-button, .lesara-button, .reali-button {
        width: 100%;
    }
    .reali-heading {
        text-align: left;
    }
    .slider-container {
        width: 100%;
        padding: 20px 20px 45px;
    }
}
@media screen and (max-width: 479px) {
    .reali-image-container {
        display: flex;
        height: 50%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex: 1;
    }
    .reali-col {
        position: relative;
        width: auto;
        height: 100%;
        justify-content: space-between;
    }
    .link-block-23 {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-flex: 0;
        -webkit-flex: 0 auto;
        -ms-flex: 0 auto;
        flex: 0 auto;
    }
    .image-36 {
        max-width: 100%;
    }
    .reali-slider {
        height: 100%;
    }
    .reali-content-container {
        position: static;
        left: 0;
        right: 0;
        display: flex;
        height: auto;
        max-width: none;
        margin-top: 0;
        margin-bottom: 0;
        flex-direction: column;
        justify-content: flex-end;
        text-align: left;
    }
    .reali-content {
        position: static;
        top: 0;
        margin-top: 0;
        margin-right: 0;
        margin-left: 0;
        padding-top: 0;
        text-align: left;
    }
    .reali-paragraph {
        text-align: left;
    }
    .tags-div-block {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        justify-items: stretch;
        flex-wrap: wrap;
        align-items: stretch;
        align-content: flex-start;
        grid-auto-flow: column dense;
        grid-auto-columns: 1fr;
        grid-column-gap: 5px;
        grid-row-gap: 5px;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: auto auto;
    }
    .reali-button {
        width: 100%;
        margin-top: 15px;
        margin-bottom: 0;
    }
    .bold-text {
        font-weight: 500;
    }
    .reali-heading {
        text-align: left;
    }
    .slider-container {
        height: 100%;
        padding: 20px 20px 45px;
    }
}