
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');

.App {

  font-family: 'IBM Plex Sans',sans-serif;

}

/*ANIMATION*/
.shake{
  animation: shake .8s;
}

.slider_container {
  display: flex;
  height: 100%;
  min-height: auto;
  padding: 5% 5% 45px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contrast{
  transition: 0.7s;
  filter: contrast(100%);
}

.contrast:hover{
  filter: contrast(200%)
}

.slider_container{
  min-height: 0;
  padding-bottom: 45px;
}


.div-block-21 h2{
  font-size: 32px;
  line-height: 36px;
  margin-top: 20px;
  font-weight: 700;
  margin-bottom: 10px;
}

@keyframes shake {
  0%  { transform:  scale(1)}
  30% { transform:  scale(0.7)}
  45% { transform:  scale(1.1)}
  65% { transform:  scale(0.98)}
  80% { transform:  scale(1.05)}
  100%{ transform:  scale(1)}
}

.company_button{
  width: 250px;
  height: 56px;
  margin-top: 15px;
  padding: 15px 25px;
  border-radius: 28px;
  background-color: transparent;
  box-shadow: 0 16px 37px -16px rgb(17 98 104 / 50%);
  font-family: 'IBM Plex Sans',sans-serif;
  color: #fff;
  font-size: 20px;
  line-height: 26px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 767px){
  .company_button{
    width: 100%;
  }
}
